* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('./backgorundPage.webp');
  background-size: cover;
  background-position: center;
}

.container {
  /* display: none; */
  max-width: 400px;
  border: none;
  border-radius: 25px;
  text-align: center;
  overflow: hidden;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.title-login,
.title-cads {
  height: 25vh;
  background-image: url('./backgorundLogin.webp');
  background-size: cover;
  background-position: center bottom;
  padding: 15px;
}

h1 {
  color: aqua;
}

.form {
  margin: 20px;
}

h2 {
  text-align: start;
  margin-bottom: 20px;
}

.input {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.link-button {
  background-color: transparent;
  border: none;
  color: #007bff;
  cursor: pointer;
  text-decoration: underline;
  margin-bottom: 10px;
}

.btnGroup {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
}

.button,
.google-button  {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.google-button {
  background-color: #db4437;
}

.error {
  color: red;
  /* margin-top: 10px; */
}